import styled from "styled-components";
import { Input } from "../../styledElements";
import { TopButton } from "../../../../features/orderedProducts/Cart/ShoppingBag/TopComponent";
import { useEffect, useState } from "react";
import { Outlet, useNavigate, useParams } from "react-router-dom";

export const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 20px;
`;

export const InputContainer = styled.div`
  display: flex;
  align-items: center;
  align-self: center;
  gap: 10px;
`;

function SingleProductAdmin() {
  const { id } = useParams();
  const [productId, setProductId] = useState("");
  const navigate = useNavigate();

  useEffect(() => {
    if (id) {
      setProductId(id);
    } else {
      setProductId("");
    }
  }, [id]);

  return (
    <Container>
      <InputContainer>
        <Input
          placeholder="Enter product ID"
          type="text"
          value={productId}
          onChange={(e) => setProductId(e.target.value)}
        />
        <TopButton type="checkout" onClick={() => navigate(productId)}>
          Fetch
        </TopButton>
      </InputContainer>
      <Outlet />
    </Container>
  );
}

export default SingleProductAdmin;
